.login__form > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px dotted rgb(198, 198, 198);
  padding: 5rem;
}
.login__form input[type="submit"] {
  margin: 1rem;
  width: 100%;
}
.id__box,
.password__box {
  display: inherit;
  flex-direction: column;
}
